import react,{useState, useEffect} from 'react';
import './App.css';
import Cpnt01 from './component/Cpnt01';
import Cpnt02 from './component/Cpnt02';

function App() {
  let cont = {
    'nav01': <Cpnt01 />,
    'nav02': <Cpnt02 />
  };

  let [content,setContent] = useState(cont['nav01']);
  function btn(e){
    let txt = e.target.textContent;

    setContent(cont[txt]);
  }

  return (
    <>
    <header>
      <nav className='menu' onClick={btn}>
        <a>nav01</a>
        <a>nav02</a>
      </nav>
    </header>

    <main>
      {content}
    </main>
    </>
  );
}

export default App;
